import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'StoneFarm',
  description:
    'Cheaper and faster than Uniswap? Discover Rice Farm, the leading DEX on Binance Smart Chain (BSC) with the best farms in DeFi.',
  image: 'https://ricefarm.fi/images/logo.png',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  switch (path) {
    case '/':
      return {
        title: `${t('Home')} | ${t('StoneFarm')}`,
      }
    case '/competition':
      return {
        title: `${t('Trading Battle')} | ${t('StoneFarm')}`,
      }
    case '/prediction':
      return {
        title: `${t('Prediction')} | ${t('StoneFarm')}`,
      }
    case '/farms':
      return {
        title: `${t('Farms')} | ${t('StoneFarm')}`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | ${t('StoneFarm')}`,
      }
    case '/vaults':
      return {
        title: `${t('Vaults')} | ${t('StoneFarm')}`,
      }
    case '/lottery':
      return {
        title: `${t('Lottery')} | ${t('StoneFarm')}`,
      }
    case '/collectibles':
      return {
        title: `${t('Collectibles')} | ${t('StoneFarm')}`,
      }
    case '/ifo':
      return {
        title: `${t('Initial Farm Offering')} | ${t('StoneFarm')}`,
      }
    case '/teams':
      return {
        title: `${t('Leaderboard')} | ${t('StoneFarm')}`,
      }
    case '/profile/tasks':
      return {
        title: `${t('Task Center')} | ${t('StoneFarm')}`,
      }
    case '/profile':
      return {
        title: `${t('Your Profile')} | ${t('StoneFarm')}`,
      }
    default:
      return null
  }
}
